import React from "react";

const PrivacyPolicy = () => {
  return (
    <div className="container my-5">
      <div className="row">
        <div className="col-md-10 col-12 mx-auto privacy">
          <h1>Privacy Policy</h1>
          <p>
            This Privacy Policy governs the manner in which the website collects,
            uses, maintains and discloses information collected from users
            (each, a “User”) of the website. This Privacy Policy applies to
            the website and all Courses offered by the website.
          </p>

          <h2>Personal identification information</h2>
          <p>
            We may collect personal identification information from Users in
            a variety of ways, including, but not limited to, when Users
            enroll in the website or a Course within the website, subscribe to a
            newsletter, and in connection with other activities, services,
            features, or resources we make available in our website. Users may
            visit the website anonymously. We will collect personal
            identification information from Users only if they voluntarily
            submit such information to us. Users can refuse to supply
            personal identification information but doing so may prevent them
            from engaging in certain website related activities.
          </p>

          <h2>How we use collected information</h2>

          <p>
            The website may collect and use Users’ personal identification
            information for the following purposes:
          </p>

          <ul>
            <li>
              To improve customer service Information you provide helps us
              respond to your customer service requests and support needs more
              efficiently.
            </li>
            <li>
              To personalize user experience We may use information in the
              aggregate to understand how our Users as a group use the
              services and resources provided in our website.
            </li>
            <li>
              To send periodic emails We may use User email addresses to send
              Users information and updates pertaining to their order.
              User email addresses may also be used to respond to User
              inquiries, questions, or other requests.
            </li>
          </ul>

          <h2>Sharing your personal information</h2>

          <p>
            We do not sell, trade, or rent User personal identification
            information to others.
          </p>

          <h2>Third party websites </h2>

          <p>
            User may find advertising or other content in our website that
            link to the websites and services of our partners, suppliers,
            advertisers, sponsors, licensors and other third parties. We do not
            control the content or links that appear on these websites and are
            not responsible for the practices employed by websites linked to or
            from our website. In addition, these websites or services, including
            their content and links, may be constantly changing. These websites
            and services may have their own privacy policies and customer
            service policies. Browsing and interaction on any other website,
            including websites which have a link to our User, is subject to
            that website's own terms and policies.
          </p>

          <h2>Changes to this Privacy Policy</h2>

          <p>
            The website has the discretion to update this Privacy Policy at any
            time. We encourage Users to frequently check this page for any
            changes. You acknowledge and agree that it is your responsibility to
            review this Privacy Policy periodically and become aware of
            modifications.
          </p>

          <h2>Your acceptance of these terms</h2>

          <p>
            By enrolling in the website, you signify your acceptance of this
            Privacy Policy. If you do not agree to this Privacy Policy, please
            do not enroll in the website. Your continued enrollment in the website
            following the posting of changes to this Privacy Policy will be
            deemed your acceptance of those changes.
          </p>
        </div>
      </div>
    </div>
  );
};

export default PrivacyPolicy;
